import Vue from "vue";
import Router from "vue-router";
import ErrorPage from "@/views/ErrorPage.vue";
import AboutApp from "@/views/AboutApp.vue";
import Home from "@/views/Home.vue";
import EnemyList from "@/views/sub/EnemyList.vue";
import EnemyUnit from "@/views/sub/EnemyUnit.vue";
import ItemList from "@/views/sub/ItemList.vue";
import ItemUnit from "@/views/sub/ItemUnit.vue";
//debug
import LangTest from "@/components/LangTest.vue";
// import EItemType from "@/assets/conf/EItemType";
import ItemCore from "@/assets/conf/ItemCore";

Vue.use(Router);

let poolRouter = [
  {
    path: "/",
    name: "home",
    alias: ["/index.html"],
    component: Home
  },
  {
    path: "/langtest",
    name: "langtest",
    component: LangTest
  },
  {
    path: "/aboutme",
    name: "aboutme",
    component: AboutApp
  },
  /**
   * enemy
   */
  {
    path: ItemCore.type.enemy.router,
    name: "enmey list",
    component: EnemyList,
    props: { type: ItemCore.type.enemy.TAG }
  },
  {
    path: ItemCore.type.enemy.router + "/:name",
    name: "enemy unit",
    component: EnemyUnit,
    props: true
  }
];
// poolRouter.join(poolRouter)
// console.log(poolRouter);

// let types = ["item","item"];
for (let type in ItemCore.type) {
  if (type == "enemy") continue;
  poolRouter.push(
    {
      path: ItemCore.type[type].router,
      name: type + " list",
      component: ItemList,
      props: {
        type: ItemCore.type[type].TAG,
        router: ItemCore.type[type].router
      }
    },
    {
      path: ItemCore.type[type].router + "/:name",
      name: type + " unit",
      component: ItemUnit,
      props: route => {
        return {
          name: route.params.name,
          type: ItemCore.type[type].TAG,
          router: ItemCore.type[type].router
        };
      }
    }
  );
}

//end
poolRouter.push({
  path: "*",
  name: "errorpage",
  component: ErrorPage
});

export default new Router({
  // mode: "history",
  base: process.env.BASE_URL,
  routes: poolRouter
});
