import router from "@/plugins/router";
import Vue from "vue";

const debug = {
  isDebug: 0
};

const hides = {
  top: 0,
  content: 0
};

const bool = {
  loading: 0
};

const targets = {
  // loadingbar: null,
  router: router
};

const urls = {
  item_unit: null
};

const paths = {
  public: process.env.BASE_URL
};

const values = {
  winH:
    window.innerHeight ||
    document.documentElement.clientHeight ||
    document.body.clientHeight,
  winW:
    window.innerWidth ||
    document.documentElement.clientWidth ||
    document.body.clientWidth,
  barH: 35,
  titleH: 35,
  //动态设置
  barAndTitleH: 0,
  contentFullH: 0,
  contentTxtH: 0, //(window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight)-70,
  contentTxtSmallH: 0,
  top: 45
};
values.contentTxtH = values.winH - values.top - 1;
values.barAndTitleH = values.barH + values.titleH;
// values.top = 45,
values.contentFullH = values.winH - values.barH;
values.contentTxtSmallH = values.contentTxtH - 50;

export default {
  install: function(Vue) {
    Vue.prototype.$conf = this;
  },
  // urls
  hides,
  values,
  targets,
  urls,
  paths,
  debug,
  bool
};
