function swiperMe() {
  // console.log($("body"), $( "body" ).on);
  // $("body").on("swipeleft",function(evt){
  //   console.log('swipeleft');
  //   // router.go(-1);
  // }); //show next hides screen1, shows screen2
  $("body").on("swiperight",function(evt){
    let pot = evt.swipestart.coords;
    if(pot[0]<10){
      console.log('swiperight',conf.targets.router);
      let router = conf.targets.router;
      let ps = router.history.current.path;
      if (ps == "/") {
        return;
      }
      let nextRouterPath = "";
      if (ps.indexOf("/about/") >= 0) {
        nextRouterPath = "/";
      } else {
        let pss = ps.split("/");
        pss.pop();
        if (pss.length >= 2) {
          nextRouterPath = pss.join("/");
        } else {
          nextRouterPath = "/";
        }
      }
      router.push(nextRouterPath);
    }
    // let ps = router.history.current.path;
    // if (ps == "/") {
    //   return;
    // }
    // let nextRouterPath = "";
    // if (ps.indexOf("/about/") >= 0) {
    //   nextRouterPath = "/";
    // } else {
    //   let pss = ps.split("/");
    //   pss.pop();
    //   if (pss.length >= 2) {
    //     nextRouterPath = pss.join("/");
    //   } else {
    //     nextRouterPath = "/";
    //   }
    // }
    // router.push(nextRouterPath);
  }); //show prev hides screen2, shows screen1
}
swiperMe();
