<template>
  <div class="loadingbar" id="loading" style="display:none;" ref="loadingbar">
    <div class="loadingBg"></div>
    <div class="loadingContent">
      <h5 class="txtwav vibe" style="margin-top:20%;">加 载 中 . . .</h5>
    </div>
  </div>
</template>

<script>
export default {
  name: "loadingbar",
  mounted() {
    this.$conf.targets.loadingbar = this;
    this.do_wave();
  },
  methods: {
    show() {
      this.$refs.loadingbar.style["display"] = "flex";
    },
    hide() {
      this.$refs.loadingbar.style["display"] = "none";
    },
    do_wave() {
      let n = document.getElementsByClassName("txtwav");
      for (var e = 0, t = n.length; e < t; e++) {
        var a = n[e],
          r = a.textContent.trim();
        a.innerHTML = null;
        this.do_wave_core(a, r);
      }
    },
    do_wave_core(n, e) {
      // console.log(n, e);
      for (var t in e) {
        var a = document.createElement("span");
        a.innerHTML = e[t] === " " ? "&nbsp;" : e[t];
        n.appendChild(a);
      }
    }
    // abc(){
    //     console.log(123123);
    // }
  }
};

