<template>
  <div>
    <div
      v-if="items"
      class="mytable"
      :style="{
        height: $conf.values.contentTxtH + 'px'
      }"
    >
      <table ref="table">
        <thead>
          <tr>
            <th class="per20">类型</th>
            <th class="per40">图</th>
            <th class="per30">名称</th>
          </tr>
        </thead>
        <tbody
          ref="tbody"
          @scroll="onscroll($event)"
          :style="{ height: $conf.values.contentTxtH + 'px' }"
        >
          <!-- @mousedown="mousedown($event)" -->
          <tr
            v-for="(item, index) in items"
            :key="index"
            :itemname="item.name"
            @click="clickunit($event)"
          >
            <td class="per20" style="font-size:16px;">
              {{ item.type }}
            </td>
            <td class="per40">
              <div style="overflow:hidden;">
                <img
                  v-show="item.img != ''"
                  style="border-radius:8px;width:120px;max-width:120px;max-height:80px;"
                  :src="
                    $conf.paths.public + 'images/others/' + item.path + item.img
                  "
                />
              </div>
            </td>
            <td class="per30" style="font-size:16px;">
              {{ item.name }}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div v-else>
      数据出错
    </div>
  </div>
</template>

<script>
import Data_Item from "@/assets/data/item/items.json";
// import EItemType from "@/assets/conf/EItemType";
import ItemCore from "@/assets/conf/ItemCore";
// console.log(Data_Item);

export default {
  name: "itemlist",
  props: {
    type: {
      type: String,
      default: ItemCore.type.item.TAG
    },
    router: {
      type: String
    }
  },
  data() {
    return {
      info: null,
      items: null
    };
  },
  created() {
    console.log("type", this.type);
    for (let key in ItemCore.type) {
      if (ItemCore.type[key].TAG == this.type) {
        this.info = ItemCore.type[key];
        break;
      }
    }
    console.log("info", this.info);
    this.items = {};
    if (this.info.type instanceof Array) {
      for (let unitType of this.info.type) {
        for (let unit of Data_Item) {
          if (unit.type == unitType && !this.items[unit.name]) {
            this.items[unit.name] = unit;
          }
        }
      }
    } else {
      for (let unit of Data_Item) {
        if (unit.type == this.info.type && !this.items[unit.name]) {
          this.items[unit.name] = unit;
        }
      }
    }
    // console.log(this.items);
  },
  mounted() {
    // console.log(this.$refs);
    this.maxHScroll =
      this.$refs.tbody.scrollHeight - this.$refs.tbody.clientHeight;
    this.$refs.tbody.scrollTop = this.$local.getPosition(this.type);
  },
  methods: {
    clickunit(evt) {
      let itemName = evt.currentTarget.getAttribute("itemname");
      this.$router.push({ path: this.router + "/" + itemName });
      console.log(itemName);
    },
    onscroll(evt) {
      let pot = evt.currentTarget.scrollTop;
      this.$local.setPosition(this.type, pot);
    }
  }
};
</script>

<style></style>
