<template>
  <div class="top" v-show="visible">
    <div class="title confirm_selection">{{ title }}</div>
    <div class="bt_menu" @click="onClickBack"></div>
  </div>
</template>

<script>
export default {
  name: "top",
  data: function() {
    return {
      visible: true,
      title: "黑暗之魂3"
    };
  },
  created() {
    this.visible = !this.$conf.hides.top;
  },
  mounted(){
    // $(".title.confirm_selection").on("swipeleft",function(evt){
    //   console.log('swipeleft');
    // });
  },
  methods: {
    onClickBack() {
      // console.log(this.$router);
      let ps = this.$router.history.current.path;
      if (ps == "/") {
        return;
      }
      let nextRouterPath = "";
      if (ps.indexOf("/about/") >= 0) {
        nextRouterPath = "/";
      } else {
        let pss = ps.split("/");
        pss.pop();
        if (pss.length >= 2) {
          nextRouterPath = pss.join("/");
        } else {
          nextRouterPath = "/";
        }
      }
      this.$router.push(nextRouterPath);
    }
  }
};
</script>
