<template>
  <!-- <div> -->
  <div style="text-align:center" v-if="!showName">
    <!-- <lu v-for="(unit,index) in data" :key="index"> -->
    <!-- <font v-for="(unit,index) in data" :key="index">{{index}}</font> -->
    <img-icon
      style="margin-right:5px;"
      v-for="(unit, index) in imgs"
      :key="index"
      :src="'./images/property/' + unit.img"
    />
    <!-- </lu> -->
  </div>
  <div style="text-align:center" v-else>
    <font v-for="(unit, index) in imgs" :key="index">
      <img-icon
        style="margin-right:2px;margin-left:5px;"
        :src="'./images/property/' + unit.img"
      />
      {{ unit.name }}
    </font>
    <!-- <font>{{unit.name}} <img-icon style="margin-right:5px;" :src="'./images/property/'+unit.img"/></font> -->
  </div>
  <!-- </div> -->
</template>

<script>
import ItemCore from "@/assets/conf/ItemCore";
// let infoProperties = {
//   雷电: "pp_mp_miracle.png",
//   闪电: "pp_mp_miracle.png",
//   冻气: "pp_sp_cold.png",
//   寒气: "pp_sp_cold.png",
//   出血: "pp_sp_blood.png",
//   流血: "pp_sp_blood.png",
//   毒素: "pp_sp_poison.png",
//   黑暗: "pp_mp_dark.png",
//   火焰: "pp_mp_spell.png",
//   咒术: "pp_mp_spell.png",
//   魔法: "pp_mp_magic.png",
//   打击: "pp_wl_dj.png",
//   钝器: "pp_wl_dj.png",
//   突刺: "pp_wl_tc.png",
//   斩击: "pp_wl_zj.png",
//   物理: "pp_wl.png",
//   诅咒: "pp_sp_dead.png",
//   法兰大剑: "pp_wl_zj.png",
//   狼骑士大剑: "pp_wl_zj.png"
// };

export default {
  install: function(Vue) {
    Vue.component("props-icon", this);
  },
  name: "propertysicon",
  props: ["data", "showName"],
  data: () => {
    return {
      // data:null,
      // dataModified:null,
      imgs: []
    };
  },
  mounted() {
    // console.log(this.data);
    if (!this.data || this.data == "无" || this.data == "") {
      return;
    }
    let dataTmp = this.data.split("，");
    for (let unit of dataTmp) {
      // console.log(unit);
      if (unit in ItemCore.propertyIco) {
        this.imgs.push({ img: ItemCore.propertyIco[unit], name: unit });
      } else {
        console.error(unit);
      }
    }
  }
};
</script>

