  <div class="content-major-body">
    <div
      class="showunit"
      :style="{ height: $conf.values.contentTxtH + 'px' }"
      v-if="enemy != null"
    >
      <!-- <div class="mark-me" @click="onMarkChange">
        <font v-if="!isMarked" class="el-icon-star-off" style="color:#fff"
          >收藏</font
        >
        <font v-else class="el-icon-star-on" style="color:#fff">已收藏</font>
      </div> -->

      <img
        v-show="enemy.img != ''"
        class="enemy-icon"
        :src="'./images/enemy/' + enemy.img"
        style="margin-top:10px;margin-bottom:5px;"
      />
      <h5 style="margin-top:10px;margin-bottom:10px;">{{ enemy.name }}</h5>
      <!-- <h6 v-show="enemy.name2">{{ enemy.name2 }}</h6> -->
      <h6>- 类型 -</h6>
      <h5 style="color:#ddd">{{ enemy.type }}</h5>

      <h6>- 弱点 -</h6>
      <h5 v-if="enemy.weak != '无'" style="color:#afa">
        <props-icon :data="enemy.weak" showName="true" />
      </h5>
      <h5 v-else style="color:#666">无</h5>

      <h6>- 抗性 -</h6>
      <h5 v-if="enemy.resist != '无'" style="color:#dd0">
        <props-icon :data="enemy.resist" showName="true" />
      </h5>
      <h5 v-else style="color:#666">无</h5>

      <h6>- 免疫 -</h6>
      <h5 v-if="enemy.immuno != '无'" style="color:#fa0">
        <props-icon :data="enemy.immuno" showName="true" />
      </h5>
      <h5 v-else style="color:#666">无</h5>

      <h6 v-show="enemy.place">- 出现地点 -</h6>
      <h5 v-show="enemy.place" style="color:#ddd">{{ enemy.place }}</h5>

      <h6>- 掉落 -</h6>
      <h5 v-if="enemy.drop" style="color:#aff">{{ enemy.drop }}</h5>
      <h5 v-else style="color:#666">无</h5>

      <h6
        v-show="enemy.desc != ''"
        style="margin-top:10px;padding-left:30px;padding-right:30px;"
      >
        {{ enemy.desc }}
      </h6>
    </div>
    <div v-else>
