</template>

<script>
// import Data_Items_Base from "@/assets/data/items/items_full.json";
import Data_Enemy from "@/assets/data/enemy/enemy.json";

export default {
  name: "enemyunit",
  props: ["name"],
  data() {
    return {
      // name: null,
      enemy: null,
      isMarked: false
      // iconUrl:null,
    };
  },
  mounted() {
    // this.name = this.$route.params.name;
    this.enemy = Data_Enemy[this.name];
    this.isMarked = this.$local.getEnemyMrk(this.enemy.name) != null;
  },
  methods: {
    onMarkChange() {
      if (this.isMarked) {
        this.$local.removeEnemyMrk(this.enemy.name);
      } else {
        this.$local.addEnemyMrk(this.enemy.name);
      }
      this.isMarked = this.$local.getEnemyMrk(this.enemy.name) != null;
    }
  }
};
</script>

