<template>
  <div id="language-test">
    <select name="lang-sel" id="lang-sel">
      <option v-for="(item, index) in langlist" :key="index" :value="index">{{
        item.name
      }}</option>
    </select>
  </div>
</template>

<script>
export default {
  name: "language-test",
  data: function() {
    return {
      langlist: [
        { name: "English", value: "en_US" },
        { name: "简体中文", value: "zh_CN" }
      ]
    };
  },
  mounted() {
    console.log(this.$i18n, this.$i18n.locate);
  },
  methods: {
    langToggle() {
      if (this.$i18n.locate == "zh_CN") {
        this.$i18n.locate = "en_US";
      } else {
        this.$i18n.locate = "zh_CN";
      }
    }
  }
};
</script>
