// import Vue from "vue";
const STORE_KEY = "DARKSOULS3-STORE";
const STORE_KEY_OTHER = "OTHER";
const STORE_KEY_POSITION = "POSITION";
// const STORE_KEY_ITEM_HISTORY = "ITEMS-HISTORY";
// const STORE_KEY_ITEM_MARK = "ITEMS-MARK";
const STORE_KEY_ENEMY_MARK = "ENEMY-MARK";

export default {
  data: null,
  __timer: null,
  install: function(Vue) {
    try {
      this.data = JSON.parse(window.localStorage.getItem(STORE_KEY)) || {};
    } catch (e) {
      console.log(e);
      this.data = {};
    }
    if (this.data == null) {
      this.data = {};
    }
    if (!this.data[STORE_KEY_OTHER]) {
      this.data[STORE_KEY_OTHER] = {};
    }
    if (!this.data[STORE_KEY_POSITION]) {
      this.data[STORE_KEY_POSITION] = {};
    }
    Vue.prototype.$local = this;
    // this.clearItems();
    //
  },
  save() {
    // window.localStorage.setItem(STORE_KEY, JSON.stringify(this.data))
    if (this.__timer) {
      window.clearTimeout(this.__timer);
      this.__timer = null;
    }
    this.__timer = window.setTimeout(this.__save_real, 1000, this);
  },
  __save_real(self) {
    console.log("real save");
    window.localStorage.setItem(STORE_KEY, JSON.stringify(self.data));
  },
  getKey(key, defValue) {
    return this.data[STORE_KEY_OTHER][key] || defValue;
  },
  setKey(key, value) {
    this.data[STORE_KEY_OTHER][key] = value;
    this.save();
  },
  removeKey(key) {
    delete this.data[STORE_KEY_OTHER][key];
    this.save();
    return true;
  },
  //
  //
  __getTable(table, id) {
    if (id && this.data[table]) {
      let idx = this.data[table].indexOf(id);
      return idx >= 0 ? this.data[table][idx] : null;
    }
    return this.data[table] || null;
  },
  __clearTable(table) {
    this.data[table] = {};
    this.save();
  },
  __addTableUnit(table, value) {
    this.__removeTableUnit(table, value, false);
    this.data[table].unshift(value);
    this.save();
  },
  __removeTableUnit(table, value, isAutoSave = true) {
    if (!this.data[table]) {
      this.data[table] = [];
    }
    const idx = this.data[table].indexOf(value);
    if (idx >= 0) {
      this.data[table].splice(idx, 1);
      if (isAutoSave) {
        this.save();
      }
      return true;
    }
    return false;
  },
  //
  //
  /**获取道具 */
  // getItemHis(itemId) {
  //   return this.__getTable(STORE_KEY_ITEM_HISTORY, itemId);
  // },
  // clearItemHis() {
  //   this.__clearTable(STORE_KEY_ITEM_HISTORY);
  // },
  // /**添加道具 */
  // addItemHis(itemId) {
  //   this.__addTableUnit(STORE_KEY_ITEM_HISTORY, itemId);
  // },
  // removeItemHis(itemId) {
  //   return this.__removeTableUnit(STORE_KEY_ITEM_HISTORY, itemId);
  // },

  /**敌人*/
  getEnemyMrk(id) {
    return this.__getTable(STORE_KEY_ENEMY_MARK, id);
  },
  clearEnemyMrk() {
    this.__clearTable(STORE_KEY_ENEMY_MARK);
  },
  addEnemyMrk(id) {
    this.__addTableUnit(STORE_KEY_ENEMY_MARK, id);
  },
  removeEnemyMrk(id) {
    return this.__removeTableUnit(STORE_KEY_ENEMY_MARK, id);
  },

  /**敌人*/
  getPosition(key) {
    const tmp = this.data[STORE_KEY_POSITION][key];
    if (!tmp) {
      return 0;
    }
    return tmp;
  },
  setPosition(key, value) {
    this.data[STORE_KEY_POSITION][key] = value;
    this.save();
  },
  removePosition(key) {
    delete this.data[STORE_KEY_POSITION][key];
    this.save();
    return true;
  }
};
