<template>
  <div>
    <div
      class="mytable"
      :style="{
        width: $conf.values.winW + 'px',
        height: $conf.values.contentTxtH + 'px'
      }"
    >
      <table ref="table">
        <thead>
          <tr>
            <th class="per10">图</th>
            <th class="per20">名称</th>
            <th class="per20">弱点</th>
            <th class="per20">抗性</th>
            <th class="per20">免疫</th>
          </tr>
        </thead>
        <tbody
          ref="tbody"
          @scroll="onscroll($event)"
          @mousedown="mousedown($event)"
          :style="{ height: $conf.values.contentTxtH + 'px' }"
        >
          <tr
            v-for="item in items"
            :key="item.id"
            :itemname="item.name"
            @click="clickunit($event)"
          >
            <td class="per10" style="">
              <div style="width:50px;height:50px;overflow:hidden;">
                <img
                  v-show="item.img != ''"
                  style="border-radius:8px;width:50px"
                  :src="$conf.paths.public + 'images/enemy/' + item.img"
                />
              </div>
            </td>
            <td class="per20" style="font-size:16px;">
              {{ item.name }}
            </td>
            <td class="per20" style="font-size:16px;">
              <!-- <imgicon src="./images/property/pp_mp_spell.png"/> -->
              <props-icon v-if="item.weak" :data="item.weak" />
              <!-- <div v-if="item.weak">{{ item.weak }}</div> -->
              <!-- <div v-else>-</div> -->
            </td>
            <td class="per20" style="font-size:16px;">
              <props-icon v-if="item.resist" :data="item.resist" />
              <!-- <div v-if="item.resist">{{ item.resist }}</div> -->
              <!-- <div v-else>-</div> -->
            </td>
            <td class="per20" style="font-size:16px;">
              <props-icon v-if="item.immuno" :data="item.immuno" />
              <!-- <div v-if="item.immuno">{{ item.immuno }}</div> -->
              <!-- <div v-else>-</div> -->
            </td>
            <!-- <td class="per40" style="font-size:14px;text-align:left;">
              {{ item.place.substr(0, 50) }}
            </td> -->
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import Data_Enemy from "@/assets/data/enemy/enemy.json";
// console.log(Data_Enemy);
import ItemCore from "@/assets/conf/ItemCore";
// console.log(Data_Enemy);

export default {
  name: "enemylist",
  props: {
    type: { type: String }
  },
  data() {
    return {
      isInput: false,
      inputStr: "",
      // publicPath: process.env.BASE_URL,
      maxHScroll: 0,
      count: 0,
      itemsBk: null,
      items: null,
      currentItem: null,
      keys: null,
      keys_tmp: null,
      keys_all: [],
      /**是否星列表 */
      isStarList: false
    };
  },
  created() {
    this.init();
  },
  mounted() {
    // console.log(this.$refs);
    // this.init();
    // console.log(document.getElementById('title').style.backgroundImage);
    this.maxHScroll =
      this.$refs.tbody.scrollHeight - this.$refs.tbody.clientHeight;
    // console.log(this.$local.getPosition(ItemCore.type.enemy),this.$refs.tbody.scrollTop);
    this.$refs.tbody.scrollTop = this.$local.getPosition(
      ItemCore.type.enemy.TAG
    );

    // console.log('$(".mytable")',$(".mytable"));
    // $(".mytable").bind("swipeleft", () => {
    //   this.$router.go(-1);
    // }); //show next hides screen1, shows screen2
    // $(".mytable").bind("swiperight", () => {
    //   this.$router.go(1);
    // }); 
  },
  beforeRouterUpdate(to, from, next) {
    // this.inti();
    next();
  },
  methods: {
    init() {
      this.items = Data_Enemy;
      // for(let i in this.items){
      //   let unit = this.items[i];
      // unit.weak = unit.weak=="无"? null : unit.weak.split("，");
      // unit.resist = unit.resist=="无"? null : unit.resist.split("，");
      // unit.immuno = unit.immuno=="无"? null : unit.immuno.split("，");
      // }
      this.itemsBk = this.items;
      this.keys = Object.keys(this.items);
      this.keys_tmp = this.keys;
      for (const key of this.keys) {
        this.keys_all.push(
          [
            this.items[key].id,
            this.items[key].name,
            this.items[key].name_py,
            this.items[key].name_en,
            this.items[key].desc
          ].join("|")
        );
        // this.keys_name_en.push(this.items[key].name_en);
        // console.log(key);
      }
      this.count = this.keys.length;
      this.currentItem = this.items[this.keys[0]];

      this.isStarList = this.$local.getKey("marklist", false);
      if (this.isStarList) {
        this.__delayStarListChange();
      }
      // console.log(this.keys_py);
    },
    clickunit(evt) {
      let itemName = evt.currentTarget.getAttribute("itemname");
      this.$router.push({ path: "/enemy/" + itemName });
      console.log(itemName);
      // this.$router.push("/items/"+itemName);
    },
    onblur(evt) {
      // console.log(evt.currentTarget);
      this.isInput = false;
    },
    mousedown(evt) {
      this.isInput = false;
    },
    onscroll(evt) {
      let pot = evt.currentTarget.scrollTop;
      this.$local.setPosition(ItemCore.type.enemy.TAG, pot);
    },
    showInput(isShow) {
      this.isInput = isShow;
      if (isShow) {
        this.$el.getElementsByTagName("input")[0].focus();
        this.$el.getElementsByTagName("input")[0].value = "";
      }
    },
    __delayStarListChange(target) {
      // this = target;
      if (this.isStarList) {
        let itemMrk = this.$local.getItemMrk();
        // console.log("itemMrk",itemMrk,itemMrk.length);
        if (itemMrk && itemMrk.length > 0) {
          let dicTmp = {};
          this.keys.forEach((unit, idx) => {
            // console.log(unit,idx);
            if (itemMrk.indexOf(this.itemsBk[this.keys[idx]].id) >= 0) {
              dicTmp[this.keys[idx]] = this.itemsBk[this.keys[idx]];
            }
          });
          this.items = dicTmp;
          this.keys_tmp = Object.keys(dicTmp);
        } else {
          let dicTmp = {
            无收藏: { id: -1, name: "无收藏", desc: "无收藏" }
          };
          this.items = dicTmp;
          this.keys_tmp = Object.keys(dicTmp);
        }
      } else {
        this.items = this.itemsBk;
        this.keys_tmp = this.keys;
      }
      // console.log(this.itemsBk[this.keys_tmp[0]]);
      this.$el.getElementsByTagName("tbody")[0].scrollTop = 0;

      const loadingbar = this.$conf.targets.loadingbar;
      loadingbar.hide();
    }
  },
  watch: {
    isStarList: function(val) {
      this.$local.setKey("marklist", val);
      const loadingbar = this.$conf.targets.loadingbar;
      loadingbar.show();
      window.setTimeout(this.__delayStarListChange, 30);
    },
    inputStr: function(val) {
      if (val == "") {
        this.items = this.itemsBk;
        this.keys_tmp = this.keys;
        this.$el.getElementsByTagName("tbody")[0].scrollTop = 0;
        return;
      }
      this.isStarList = false;
      val = val.replace(/\+/, "\\+");
      val = val.toLowerCase();
      const regex = new RegExp(val, "g");
      let dicTmp = {};
      let matchIdx = this.keys_all.filter((unit, index) => {
        if (unit.match(regex)) {
          dicTmp[this.keys[index]] = this.itemsBk[this.keys[index]];
          return true;
        }
      });
      let keys_tmp = Object.keys(dicTmp);
      if (keys_tmp.length != 0) {
        this.items = dicTmp;
        this.keys_tmp = keys_tmp;
      } else {
        this.items = this.itemsBk;
        this.keys_tmp = this.keys;
      }
      this.$el.getElementsByTagName("tbody")[0].scrollTop = 0;
    }
  }
  // computed:{
  //   oninput:function(){
  //     console.log(this.inputStr);

  //   }
  // }
};
</script>

<style></style>
